import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State, Getter } from 'vuex-class';

// import flatButton from 'muse-ui/flatButton';
// import Dialog from 'muse-ui/dialog';
import textField from 'muse-ui/textField';
import popover from 'muse-ui/popover';
import { menu, menuItem } from 'muse-ui/menu';

import Api from '~/api';
import { ToastMsg } from '~/entity/toast';
import { IAddFriendItem } from '~/entity/friend/Ifriend';
import { Ifriend } from 'src/entity/friend';
import { IFriendListParams, IPersonalParams } from 'src/entity/route_params';
import { Watch } from 'vue-property-decorator';
import { ADD_FRIEND } from '~/store/types';
import { MyConversation } from '~/entity/my_message';
import { IAddFriendImpParams, IDialogParams } from '~/entity/route_params/index';

function getChar(charCode: number) {
    return String.fromCharCode(charCode + 65);
}

// function showName(friend: Ifriend) {
//     if (friend.nickname && friend.nickname.length > 0) {
//         return friend.nickname;
//     } else {
//         return friend.realname;
//     }
// }

// function showNickName(nickname: string) {
//     if (nickname && nickname.length > 0) {
//         return nickname;
//     } else {
//         return '';
//     }
// }

@Component({
    components: {
        // 'mu-flat-button': flatButton,
        // 'mu-dialog': Dialog,
        'mu-text-field': textField,
        'mu-popover': popover,
        'mu-menu': menu,
        'mu-menu-item': menuItem
    },
    filters: {
        getChar
        // showName
    }
})
export default class FriendHistoryComp extends Vue {

    friendList: Ifriend[][] = [];

    hintText: string = null;

    params: IFriendListParams = null;

    selectedFriend: Ifriend = null;

    nickname: string = null;

    /* 刷新 */
    refreshingFriends = false;

    friendsContainer: HTMLElement = null;

    /* 对话框 */
    deleteDialog = false;

    alterDialog = false;

    // popover触发元素
    triggerPopover = null;
    fidTemp = '';
    open = false;
    // popover列表显示位置
    anchorOrigin = {
        vertical: 'bottom',
        horizontal: 'right'
    };
    targetOrigin = {
        vertical: 'top',
        horizontal: 'right'
    };

    @Action('showToast')
    showToast;

    @Action('addFriend')
    addFriend;

    @Action('getFriendList')
    getFriendList;

    $refs: {
        friendsContainer: any,
        Test: any
    };

    // @Watch('fidTemp')
    // showfid(newVal, oldVal) {
    //     console.log(`newVal: ${newVal}; old: ${oldVal}`);
    // }
    mounted() {
        this.friendsContainer = this.$refs['friendsContainer'];
    }

    activated() {
        // debugger;
        const params: IFriendListParams = this['$route'].params;
        this.params = params;
        this.setFriends(params);              
    }

    deactivated() {
        this.open = false;
        this.closeAlterDialog();
        this.closeDeleteDialog();
    }
    
    setFriends(params: IFriendListParams) {
        if (params.action === 'singleMale') {
            // this.friendList = this['$store'].state.user.singleMaleFriends;
            this.friendList = this.$store.getters.singleMaleFriends;
            this.hintText = '暂时没有单身男性好友！';
        } else if (params.action === 'singleFemale') {
            // this.friendList = this['$store'].state.user.singleFemaleFriends;
            this.friendList = this.$store.getters.singleFemaleFriends;
            this.hintText = '暂时没有单身女性好友！';
        } else if (params.action === 'nonSingle') {
            // this.friendList = this['$store'].state.user.nonSingleFriends;
            this.friendList = this.$store.getters.nonSingleFriends;
            this.hintText = '暂时没有非单身好友';
        }     
    }

    /**
     * 刷新好友列表
     */
    async refreshFriend() {
        this.refreshingFriends = true;
        try {
            await this.getFriendList();
            this.setFriends(this.params);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingFriends = false;
        }
    }

    toPersonalPage(friend: Ifriend) {
        const params: IPersonalParams = {
            action: 'otherUser',
            id: friend.fid
        };
        this['$router'].push({
            name: 'personalPage',
            params
        });
    }

    // 进入聊天页面
    // toDialogPage(friend: Ifriend) {
    //     const params: IDialogParams = {
    //         conversationId: friend.fid,
    //         conversationType: MyConversation.CONVERSATION_TYPE.SINGLE + ''
    //     };
    //     this.$router.push({
    //         name: 'dialog',
    //         params
    //     });
    // }
    
    // 弹出删除菜单
    toggleList(event, fid) {
        this.fidTemp = fid;
        this.triggerPopover = event.target;
        // this.triggerPopover = document.getElementById('MoreOption' + fid);
        this.open = !this.open;
    }
    // 关闭删除菜单
    handleClose(e) {
        this.open = false;
        this.fidTemp = '';
    }

    /* 删除好友对话框 */
    wantDeleteFriend() {
        this.open = false;
        this.deleteDialog = true;        
    }

    closeDeleteDialog() {
        this.deleteDialog = false;
        this.fidTemp = '';
    }

    cancelDelete() {
        this.closeDeleteDialog();
    }

    async confirmDelete() {
        // 删除好友
        try {
            const res = await Api.deleteFriend(this.fidTemp);
            this.refreshFriend();
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.closeDeleteDialog();
        }
    }

    /* 修改备注对话框 */
    handleInputOverflow(value) {
        // console.log(value);
        if (value) {
            this.nickname = this.nickname.slice(0, 10);
        }
    }

    alterNickName(friend: Ifriend) {
        this.selectedFriend = friend;
        this.nickname = friend.nickname;
        this.alterDialog = true;
    }

    closeAlterDialog() {
        this.alterDialog = false;
    }

    cancelAlterDialog() {
        this.closeAlterDialog();
    }

    async confirmAlterDialog() {
        // 修改备注
        try {
            const nickname = this.nickname;
            const res = await Api.alterNickName(nickname, this.selectedFriend.fid);
            // this.selectedFriend.nickname = nickname;
            this.refreshFriend();
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.closeAlterDialog();
        }
    }

    // 前往好友点评页面留言
    toEvaluationPage(friend: Ifriend) {
        const params: IAddFriendImpParams = {
            id: friend.fid,
            sex: friend.sex,
            action: 'fromFriendList'
        };
        this['$router'].push({
            name: 'addComment',
            params
        });
    }
}



